/* ✨ 전체 앱 컨테이너 */
.app-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  color: #ffffff;
  text-align: center;
}

/* ✨ 배경화면 (고급스러운 다크 테마) */
body {
  background: radial-gradient(circle, #0f172a, #1e293b, #334155);
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}

/* ✨ 카테고리 그리드 - 8행 5열 (반응형 지원) */
.category-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* 자동 조정 */
  gap: 15px;
  justify-content: center;
  margin-top: 20px;
}

/* ✨ 카테고리 버튼 */
.category-button {
  padding: 14px 20px;
  background: linear-gradient(135deg, #1e40af, #3b82f6);
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  min-height: 55px;
  text-align: center;
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.category-button:hover {
  background: linear-gradient(135deg, #3b82f6, #60a5fa);
  transform: scale(1.05);
}

.category-button.selected {
  background: linear-gradient(135deg, #e94560, #f87171);
  box-shadow: 0px 4px 8px rgba(233, 69, 96, 0.8);
}

/* ✨ 질문 박스 */
.question-box {
  background-color: #1e293b;
  padding: 20px;
  margin: 20px 0;
  border-radius: 12px;
  border: 2px solid #3b82f6;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
  color: #e2e8f0;
}

/* ✨ 버튼 공통 스타일 */
.start-button, .record-button, .control-button {
  margin: 10px;
  padding: 15px 35px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  transition: 0.3s;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
}

.start-button {
  background: #1e40af;
  color: white;
}

.start-button:hover {
  background: #3b82f6;
  transform: scale(1.1);
}

.record-button {
  background: #e94560;
  color: white;
}

.record-button:hover {
  background: #f87171;
  transform: scale(1.1);
}

.control-button {
  background: #1e293b;
  color: #ffffff;
}

.control-button:hover {
  background: #64748b;
}

/* ✨ 버튼을 한 행에 배치 */
.button-group {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

/* ✨ 피드백 박스 */
.feedback-box {
  background-color: #0f172a;
  padding: 18px;
  margin-top: 15px;
  border-radius: 8px;
  border-left: 4px solid #facc15;
  text-align: center;
  color: #f8fafc;
  font-size: 16px;
}

/* ✨ 피드백 텍스트 */
.feedback-text {
  color: #e2e8f0;
  font-weight: bold;
  font-size: 18px;
}

/* ✨ 속도 조절 버튼 */
.speed-control {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 15px;
}

.speed-button {
  padding: 10px 20px;
  background: linear-gradient(135deg, #3b82f6, #60a5fa);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s ease-in-out;
}

.speed-button:hover {
  background: linear-gradient(135deg, #1e40af, #3b82f6);
  transform: scale(1.1);
}

/* ✨ 음성 변환 버튼 */
.convert-button {
  margin-top: 20px;
  padding: 14px 24px;
  background: linear-gradient(135deg, #f59e0b, #facc15);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.convert-button:hover {
  background: linear-gradient(135deg, #facc15, #fbbf24);
  transform: scale(1.1);
}

/* ✨ 반응형 지원 (모바일 최적화) */
@media (max-width: 768px) {
  .category-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .start-button, .record-button, .control-button {
    padding: 12px 20px;
    font-size: 1rem;
  }

  .category-button {
    min-height: 50px;
    font-size: 14px;
  }

  .feedback-box {
    font-size: 14px;
  }
}
